  color: rgba(0, 0, 0, 0.5);
}

.sidebar {
  margin-left: 30px;
}
.sidebar .person-name,
#thx-news .person-name,
#thx-news .person-name span {
  font-size: 16px;
  letter-spacing: 0;
  width: 135px;
  margin-top: 3px;
  line-height: 1.2;
  color: #1d1d1b;
  word-wrap: break-word;
  font-family: "Effra_Regular";
}

.head-green,
.head-red,
.head-yellow {
  position: relative;
  height: 102px;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  align-content: stretch;
}
.sidebar .angle-right {
  margin-bottom: 25px;
}
.angle-right:after {
  content: "";
  position: absolute;
  border: 100px solid transparent;
  border-bottom: 100px solid green;
  border-left: 100px solid green;
  transform: matrix(1, -0.06, 0, 0.51, 0, 7);
  top: -62px;
  left: 100%;
}
.angle-left:after {
  content: "";
  position: absolute;
  transform: matrix(1, 0.06, 0, 0.51, 0, -5);
  top: -50px;
  right: 100%;
  border: 100px solid transparent;
  border-right: 100px solid green;
  border-bottom: 100px solid green;
}

.angle-right.head-green:after {
  border-bottom: 100px solid #4e817b;
  border-left: 100px solid #4e817b;
}
.angle-right.head-yellow:after {
  border-bottom: 100px solid #c09c09;
  border-left: 100px solid #c09c09;
}
.angle-left.head-red:after {
  border-right: 100px solid #aa0f18;
  border-bottom: 100px solid #aa0f18;
}

.head-green {
  background-color: #5d9d95;
}
.head-red {
  background-color: #d1111d;
  margin-bottom: 25px;
}
.head-yellow {
  background-color: #e9bd00;
}
.btn-more {
  background-color: #626262;
  color: #fff;
  font-family: "Effra_Regular";
  border-radius: 19px;
  padding: 2px 5px;
  text-align: center;
  width: 56px;
  margin: 30px auto 10px auto;
  transition: 0.25s;
  cursor: pointer;
  font-size: 14px;
}
.btn-more:hover {
  background-color: #3e3c3c;
}
.btn-filter {
  transition: 0.25s;
  color: #fff;
  border: solid 1px #fff;
  font-family: "Effra_Regular";
  border-radius: 25px;
  padding: 5px 12px;
  letter-spacing: 0.05em;
  margin: 10px;
  cursor: pointer;
  text-align: center;
}
.btn-filter:hover,
.btn-filter.active {
  background-color: #fff;
}
.head-green .btn-filter:hover,
.head-green .btn-filter.active {
  color: #5d9d95;
}
.head-yellow .btn-filter:hover,
.head-yellow .btn-filter.active {
  color: #e9bd00;
}

.desc {
  font-family: "Effra_Medium";
  text-transform: uppercase;
  font-size: 11px;
  letter-spacing: 0.04em;
  text-align: center;
  color: #1d1d1b;
  margin-top: 10px;
  border-bottom: none;
}

.loading {
  background-image: url("images/loading.gif");
  height: 50px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #f8f8f8;
}

